/*eslint-disable*/
import React from 'react';
import Types from 'prop-types';

import {
  formatWithPattern,
  getNextGroupedSymbols,
  getSymbolsInSelection,
  pushCursorToStack,
  unformatWithPattern,
} from '../common/textFormat';

import { HistoryNavigator } from './utils';

class InputWithPattern extends React.Component {
  static propTypes = {
    pattern: Types.string,
    className: Types.string,
    placeholder: Types.string,
    onChange: Types.func.isRequired,
  };

  static defaultProps = {
    className: '',
    pattern: '',
    placeholder: '',
  };

  constructor(props) {
    super(props);
    // Can be anything with at least two chars.
    this.controlChar = '@@';
    this.input = React.createRef();
    this.state = {
      value: '',
      isCutting: false,
      isPasting: false,
      isRedo: false,
      isUndo: false,
      pastedLength: 0,
      historyNavigator: new HistoryNavigator(),
      prevPattern: props.pattern,
      previousValue: '',
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (prevState.prevPattern !== nextProps.pattern) {
      const { value, prevPattern, historyNavigator } = prevState;
      const { pattern } = nextProps;
      const unFormattedValue = unformatWithPattern(value, prevPattern);
      const formattedValue = formatWithPattern(unFormattedValue, pattern);
      historyNavigator.reset();

      return {
        prevPattern: pattern,
        value: formattedValue,
        isCutting: false,
        isPasting: false,
        isUndo: false,
        isRedo: false,
        pastedLength: 0,
      };
    }
    return null;
  }

  setCursorPosition = select => {
    setTimeout(() => {
      this.input.current.selectionStart = select;
      this.input.current.selectionEnd = select;
    }, 0);
  };

  handleOnKeyDown = e => {
    const { selectionStart, selectionEnd } = this.input.current;
    const { historyNavigator } = this.state;
    const { pattern, onChange } = this.props;

    const charCode = String.fromCharCode(e.which).toLowerCase();
    let lastKey = e.key;
    if ((e.ctrlKey || e.metaKey) && charCode === 'z') {
      const value = historyNavigator.getHistory(!e.shiftKey ? 'undo' : 'redo');
      this.setState({ value, isUndo: !e.shiftKey, isRedo: e.shiftKey });
      // onChange(unformatWithPattern(value, pattern));
    } else if (e.ctrlKey && charCode === 'd') {
      lastKey = 'Delete';
    } else {
      this.resetEvents();
    }

    this.setState({
      lastKey,
      selectionStart,
      selectionEnd,
    });
  };

  handleOnPaste = e => {
    let pastedChar = e.clipboardData.getData('Text').split('');
    const { pattern } = this.props;
    // Remove symbols from cut text.
    pastedChar = pastedChar.filter(el => pattern.split('').indexOf(el) === -1);

    this.setState({ isPasting: true, pastedLength: pastedChar.length });
  };

  handleOnCut = () => {
    this.setState({ isCutting: true });
  };

  resetEvents = () => {
    this.setState({
      isCutting: false,
      isPasting: false,
      isUndo: false,
      isRedo: false,
      pastedLength: 0,
    });
  };

  handleOnChange = e => {
    const { isRedo, isUndo } = this.state;

    if (isUndo || isRedo) {
      this.resetEvents();
      return;
    }

    const { historyNavigator, selectionStart, selectionEnd, pastedLength } = this.state;

    const { value } = e.target;
    const { pattern, onChange } = this.props;

    let unformattedValue = unformatWithPattern(value, pattern);
    const newKey = this.determineAction(unformattedValue);

    if (newKey === 'Backspace' || newKey === 'Delete') {
      unformattedValue = this.handleDelete(
        unformattedValue,
        selectionStart,
        selectionEnd,
        pattern,
        newKey,
      );
    }

    const unformattedValueWithCursor = pushCursorToStack(
      unformattedValue,
      newKey,
      selectionStart,
      selectionEnd,
      pattern,
      pastedLength,
      this.controlChar,
    );

    this.handleCursor(unformattedValueWithCursor);

    const formattedValue = formatWithPattern(unformattedValue, pattern).join('');

    //console.log(unformattedValue);

    this.setState(
      { value: formattedValue },
      this.resetEvents(),
      historyNavigator.setHistory(formattedValue),
      onChange(unformattedValue),
    );
  };

  handleDelete = (valueStack, selectionStart, selectionEnd, pattern, key) => {
    const newStack = [...valueStack];
    let startPosition = selectionStart - getSymbolsInSelection(0, selectionStart, pattern);

    let toDelete = 0;
    if (selectionStart === selectionEnd) {
      let direction = 'default';
      if (key === 'Backspace') {
        startPosition -= 1;
        direction = 'reverse';
      }
      let nextGroupedSymbols = getNextGroupedSymbols(selectionStart, pattern, direction);

      if (nextGroupedSymbols) {
        toDelete = 1;
      }

      newStack.splice(startPosition, toDelete);
    }

    return newStack;
  };

  handleCursor = stackUnformattedWithCursor => {
    const { pattern } = this.props;
    const stackFormattedWithCursor = formatWithPattern([...stackUnformattedWithCursor], pattern);
    this.setCursorPosition(stackFormattedWithCursor.indexOf(this.controlChar));
  };

  determineAction = stackUnformatted => {
    const { isPasting, isCutting, lastKey } = this.state;
    let newKey = lastKey;
    if (isPasting) {
      newKey = 'Paste';
    } else if (isCutting) {
      newKey = 'Cut';
    } else {
      newKey = this.androidBackSpaceDetector(stackUnformatted);
    }
    return newKey;
  };

  // This is a fix for Android Browsers. At the moment there's no a real way to detect the backspace other than measure
  // the value length.
  androidBackSpaceDetector = stackUnformatted => {
    const { pattern } = this.props;
    let { lastKey, value } = this.state;
    if (
      lastKey !== 'Backspace' &&
      lastKey !== 'Delete' &&
      stackUnformatted.length <= unformatWithPattern(value, pattern).length
    ) {
      return 'Backspace';
    }
    return lastKey;
  };

  render() {
    const { placeholder, className } = this.props;
    const { value } = this.state;

    return (
      <input
        ref={this.input}
        className={className}
        value={value}
        placeholder={placeholder}
        onPaste={e => this.handleOnPaste(e)}
        onKeyDown={e => this.handleOnKeyDown(e)}
        onChange={e => this.handleOnChange(e)}
        onCut={() => this.handleOnCut()}
      />
    );
  }
}

export default InputWithPattern;



